import { CSSProperties, useMemo } from "react"
import { SxProps } from "@mui/system"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

type UseBriefNotificationTimeStepDataState = {
  width: string | number
  containerSx: SxProps
  timezoneContainerSx: SxProps
  timezoneMenuStyle: CSSProperties
}

const useBriefNotificationTimeStepData = (): UseBriefNotificationTimeStepDataState => {
  const { isMobile } = useDeviceInfo()

  return useMemo(() => {
    const width = isMobile ? "100%" : 400

    const containerSx = {
      mt: 1.5,
      display: isMobile ? undefined : "flex",
      gap: 2,
      "& > div": { flexGrow: 1 },
      "& > div p": { color: "text.secondary" },
    }

    const timezoneContainerSx = { mt: isMobile ? 3 : 0 }

    const timezoneMenuStyle = { width: isMobile ? "auto" : width }

    return { width, containerSx, timezoneContainerSx, timezoneMenuStyle }
  }, [isMobile])
}

export { useBriefNotificationTimeStepData }
export type { UseBriefNotificationTimeStepDataState }
